html,
body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  background: #040404;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}

h3 {
  padding: 0 15px;
}

h4 {
  font-weight: 100;
}

.btn-toggle {
  background: blue;
  color: white;
  padding: 10px;
  border: 0;
  border-radius: 5px;
  text-transform: uppercase;
  margin: 5px 0;
  outline: none;
  cursor: pointer;
}
.btn-toggle:hover, .btn-toggle:active {
  background: rgb(0, 0, 229.5);
}

.examples {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}
.examples .example {
  width: 320px;
  height: auto;
  margin: 0 10px 10px 0;
  flex: 0 0 320px;
}

.canvas {
  width: 320px;
  height: 172px;
  border: 4px solid #333;
  border-radius: 5px;
  overflow: hidden;
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.canvas[controls].animated {
  cursor: pointer;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.6, 1);
}
.canvas[controls].animated.paused {
  opacity: 0.5;
}
.canvas[controls].animated.paused:hover {
  opacity: 0.75;
}
.canvas[controls].animated.playing {
  border-color: blue;
}

.renderer .canvas {
  width: 640px;
  height: 344px;
}
.renderer .examples .example {
  width: 640px;
  flex: 0 0 640px;
}

video {
  width: 320px;
  height: 172px;
  border: 4px solid #333;
  border-radius: 5px;
  overflow: hidden;
  box-sizing: border-box;
  object-fit: cover;
}

* {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}